"use client";
import * as XLSX from "xlsx";
import { addDoc, collection } from "firebase/firestore";
import { db } from "../app/lib/firebase";
import { useState } from "react";

export default function UploadExcel() {
  const [file, setFile] = useState<File | null>(null);
  const [status, setStatus] = useState<string>("");

  // 1Ô∏è‚É£ Captura do arquivo
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files || e.target.files.length === 0) {
      setStatus("Nenhum arquivo selecionado");
      return;
    }

    setFile(e.target.files[0]);
    setStatus(`Arquivo selecionado: ${e.target.files[0].name}`);
  };

  // 2Ô∏è‚É£ Clique no bot√£o ENVIAR
  const handleSubmit = async () => {
    // üîé VALIDA√á√ÉO 1 ‚Äî arquivo existe?
    if (!file) {
      setStatus("Erro: nenhum arquivo selecionado");
      return;
    }

    // üîé VALIDA√á√ÉO 2 ‚Äî formato
    if (!file.name.endsWith(".xlsx") && !file.name.endsWith(".csv")) {
      setStatus("Erro: formato inv√°lido (use .xlsx ou .csv)");
      return;
    }

    try {
      setStatus("Lendo arquivo...");

      // üîé VALIDA√á√ÉO 3 ‚Äî leitura do arquivo
      const buffer = await file.arrayBuffer();

      setStatus("Processando planilha...");

      const workbook = XLSX.read(buffer);
      const sheetName = workbook.SheetNames[0];

      // üîé VALIDA√á√ÉO 4 ‚Äî existe aba?
      if (!sheetName) {
        setStatus("Erro: planilha sem abas");
        return;
      }

      const sheet = workbook.Sheets[sheetName];
      const data = XLSX.utils.sheet_to_json(sheet);

      // üîé VALIDA√á√ÉO 5 ‚Äî tem dados?
      if (data.length === 0) {
        setStatus("Erro: planilha vazia");
        return;
      }

      setStatus(`Salvando ${data.length} registros no banco...`);

      // üîé VALIDA√á√ÉO 6 ‚Äî salvando no Firebase
      for (const row of data as any[]) {
        if (!row.nomeEvento || !row.data) {
          console.warn("Linha ignorada:", row);
          continue;
        }

        await addDoc(collection(db, "eventos"), row);
      }

      setStatus("Upload realizado com sucesso ‚úÖ");
    } catch (error) {
      console.error(error);
      setStatus("Erro ao processar o arquivo ‚ùå");
    }
  };

  return (
    <div className="card">
      <h2>Upload da Planilha</h2>

      <input
        type="file"
        accept=".xlsx,.csv"
        onChange={handleFileChange}
      />

      <br /><br />

      <button onClick={handleSubmit}>
        Enviar
      </button>

      <p style={{ marginTop: "10px" }}>{status}</p>
    </div>
  );
}